import React, { Component } from 'react'
import { Button, Snackbar, Dialog, DialogTitle, DialogActions, DialogContent, TextField} from '@material-ui/core';

import ContactMailIcon from '@material-ui/icons/ContactMail';
import DeleteIcon from '@material-ui/icons/Delete';

import nobook from '../../media/nobook.png'

const axios = require('axios');

export class CurrentBookings extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            notification: '',
            deleteId: '',
            editBooking: false,
            confirmDelete: false,

            selectedBooking: '',
            previousBooking: '',

            bookings: [],
            rawBookings: [],

            loaded: false,

            openContact: false,
            contactMessage: '',

            errors: '',
            success: ''
        }

        if (typeof window !== 'undefined' && !this.state.loaded) {
            if (!document.querySelector('#google-maps1')) {
                this.loadScript(
                'https://maps.googleapis.com/maps/api/js?key=AIzaSyCxyLmUgesPbcxC2g5fj0yqjlhAFVxCdTQ&libraries=places',
                document.querySelector('head'),
                'google-maps1',
                );
            }
            this.setState({loaded: true})
        }

        this.contact = this.contact.bind(this);
        this.showContact = this.showContact.bind(this);
        this.cancelContact = this.cancelContact.bind(this);
        this.getFormattedDate = this.getFormattedDate.bind(this);
        this.updateContactMessage = this.updateContactMessage.bind(this);
        this.deleteBooking = this.deleteBooking.bind(this);
    }

    loadScript(src, position, id) {
        if (!position) {
          return;
        }
      
        const script = document.createElement('script');
        script.setAttribute('async', '');
        script.setAttribute('id', id);
        script.src = src;
        position.appendChild(script);
    }

    componentDidMount() {
        this.reRender();
    }

    deleteBooking(id) {
        this.setState({confirmDelete: true, deleteId: id})
    }

    actuallyDelete() {
        if (new Date().getTime() + 1000*60*60*41 > new Date(this.state.rawBookings.find(element => element._id === this.state.deleteId).date).getTime()) {
            this.setState({notification: 'Cannot cancel booking 48 hours beforehand.', confirmDelete: false, deleteId: ''})
            setTimeout(() => this.setState({notification: ''}), 4000);
        } else {
            if (this.state.confirmDelete) {
                this.setState({confirmDelete: false})
                axios.delete(`/bookings/${this.state.deleteId}`).then(() => {this.reRender(); this.setState({notification: 'Successfully cancelled booking.'})});
                setTimeout(() => this.setState({notification: ''}), 4000);
            }
        }
    }

    showContact(booking) {
        this.setState({openContact: true, success: '', selectedBooking: booking});
        booking !== this.state.previousBooking && this.setState({contactMessage: ''});
    }

    updateContactMessage(event) {
        if (event.target.value.length < 5000) {
            this.setState({contactMessage: event.target.value})
        }
    }

    contact(booking) {
        axios.post('/bookings/contact', {name: booking.first_name + " " + booking.last_name, email: booking.email, body: this.state.contactMessage})
        .then((res) => {
            this.setState({success: "Successfully sent contact message", contactMessage: ''})
        }).catch((err) => {
            this.setState({errors: "Error sending contact message, please try again later."})
        })
    }

    cancelContact(booking) {
        this.setState({openContact: false, previousBooking: booking})
    }

    getFormattedDate(date) {
        var year = date.getFullYear();
        
        var month = (1 + date.getMonth()).toString();
        month = month.length > 1 ? month : '0' + month;
        
        var day = date.getDate().toString();
        day = day.length > 1 ? day : '0' + day;
        
        var time = '';
        if (parseInt(date.toISOString().split(' ')[0].substring(11, 13)) > 12) {
            if (parseInt(date.toISOString().split(' ')[0].substring(11, 13)) - 12 > 9) {
                time = ((parseInt(date.toISOString().split(' ')[0].substring(11, 13)) - 12) + date.toISOString().split(' ')[0].substring(14, 16) + 'PM'); 
            } else {
                time = ('0' + (parseInt(date.toISOString().split(' ')[0].substring(11, 13)) - 12) + date.toISOString().split(' ')[0].substring(13, 16) + 'PM'); 
            }
        } else {
            time = (date.toISOString().split(' ')[0].substring(11, 16) + 'AM');
        }

        return month + '/' + day + '/' + year + ' ' + time;
    }

    reRender() {
        axios.get('/currentbookings')
            .then(res => {
                if (res.status === 200 || res.status === 304) {
                    res.data.sort((a, b) => {
                        if (new Date(a.date) < new Date(b.date)) {
                            return -1;
                        } else {
                            return 1; 
                        }
                    })

                    const bookingsMapped = res.data.map(booking => {
                        var urlLocation = booking.location.replace(" ", "+");
                        const staticMapLink = "https://maps.googleapis.com/maps/api/staticmap?&maptype=hybrid&size=600x400&markers=color:red%7C" + urlLocation + "&zoom=18&key=AIzaSyAXnO5_29oeGqNIchVstCYS29QCI-wBJ0Q";
                        return (
                        <div className="indivBooking" key={booking._id}>
                            <div className="icons">
                                    <ContactMailIcon onClick={() => this.showContact(booking)}/>
                                    <DeleteIcon onClick={() => this.deleteBooking(booking._id)}/>
                            </div>
                            <div className="topRow">
                                <p>{booking.first_name} {booking.last_name}, {booking.email}</p>
                            </div>
                            <p><span>Phone # : </span>{booking.phone_num}</p>
                            <p><span>Date: </span>{this.getFormattedDate(new Date(booking.date))}</p>
                            <p><span>Location: </span>{booking.location}</p>
                            <p><span>Type: </span>{booking.type}</p>
                            <p><span>Package:</span> {booking.package}</p>
                            <p className="notes"><span>Notes: </span>{booking.notes.length === 0 ? "[None]" : booking.notes}</p>
                            <a href={`https://www.google.com/maps/place/${urlLocation}`} target="_blank" rel="noreferrer"><img src={staticMapLink} className="map" alt="map"/></a>
                        </div>
                        );
                    })
                    
                    this.setState({rawBookings: res.data, bookings: bookingsMapped, confirmDelete: false})
                }
            }).catch(err => {})
    }

    render() {
        return (
            <div className="CurrentBookings Booked">
                {this.state.bookings.length > 0 ? this.state.bookings : <div className="emptyPanel"><img className="emptyPanel" src={nobook} alt="blank clipboard" /><h2>No Bookings.</h2></div>}
                <Snackbar
                    open={this.state.notification.length > 0}
                    anchorOrigin={{vertical: 'top', horizontal: 'center'}}
                    message={this.state.notification}
                    autoHideDuration={4000}
                />
                <Dialog className="deleteDialog" open={this.state.confirmDelete}>
                    <DialogTitle >Cancel Booking?</DialogTitle>
                    <DialogActions className="buttons">
                    <Button onClick={() => {this.setState({confirmDelete: false, deleteId: ''})}} variant="contained" color="secondary">
                        No
                    </Button>
                    <Button onClick={() => this.actuallyDelete()} variant="contained" color="primary">
                        Yes
                    </Button>
                    </DialogActions>
                </Dialog>
                <Dialog className="bookingContact" open={this.state.openContact}>
                    <DialogTitle>Contact Customer</DialogTitle>
                    <DialogContent>
                        <TextField
                            className="notes"
                            label="Enter Message Here"
                            multiline
                            rows={10}
                            variant="outlined"
                            value={this.state.contactMessage}
                            onChange={this.updateContactMessage}
                        />
                        {this.state.errors.length > 0 && <div className="errors"><p>{this.state.errors}</p></div>}
                        {this.state.success.length > 0 && <div className="success"><p>{this.state.success}</p></div>}
                    </DialogContent>
                    <DialogActions className="buttons">
                    <Button onClick={this.cancelContact} variant="contained" color="secondary">
                        Cancel
                    </Button>
                    <Button onClick={() => this.contact(this.state.selectedBooking)} variant="contained" color="primary">
                        Send Message
                    </Button>
                    </DialogActions>
                </Dialog>
            </div>
        )
    }
}

export default CurrentBookings
