import React, { Component } from 'react'
import { Button, TextField } from '@material-ui/core';

const axios = require('axios');

const priceRegex = /^(\d{1,3})?(,?\d{3})*(\.\d{2})?$/;

export class Edit extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            standardPrice: 0,
            premiumPrice: 0,
            exclusivePrice: 0,
            smallPhoto: 0,
            mediumPhoto: 0,
            largePhoto: 0,
            superLargePhoto: 0,
            changed: false,

            standardPriceValid: true,
            premiumPriceValid: true,
            exclusivePriceValid: true,
            smallPhotoValid: true,
            mediumPhotoValid: true,
            largePhotoValid: true,
            superLargePhotoValid: true,

            errors: [],
            success: ''
        }

        this.updateStandardPrice = this.updateStandardPrice.bind(this);
        this.updatePremiumPrice = this.updatePremiumPrice.bind(this);
        this.updateExclusivePrice = this.updateExclusivePrice.bind(this);
        this.updateSmallPrice = this.updateSmallPrice.bind(this);
        this.updateMediumPrice = this.updateMediumPrice.bind(this);
        this.updateLargePrice = this.updateLargePrice.bind(this);
        this.updateSuperLargePrice = this.updateSuperLargePrice.bind(this);
        this.save = this.save.bind(this);
    }
    
    componentDidMount() {
        axios.get('/edits').then(res => {
            this.setState({
                standardPrice: res.data.package_prices[0],
                premiumPrice: res.data.package_prices[1],
                exclusivePrice: res.data.package_prices[2],
                smallPhoto: res.data.photo_prices[0],
                mediumPhoto: res.data.photo_prices[1],
                largePhoto: res.data.photo_prices[2],
                superLargePhoto: res.data.photo_prices[3]
            })
        }).catch(err => {
        })
    }

    updateStandardPrice(event) {
        this.setState({standardPrice: event.target.value, standardPriceValid: true, changed: true});
    }

    updatePremiumPrice(event) {
        this.setState({premiumPrice: event.target.value, premiumPriceValid: true, changed: true});
    }

    updateExclusivePrice(event) {
        this.setState({exclusivePrice: event.target.value, exclusivePriceValid: true, changed: true});
    }

    updateSmallPrice(event) {
        this.setState({smallPhoto: event.target.value, smallPhotoValid: true, changed: true});
    }

    updateMediumPrice(event) {
        this.setState({mediumPhoto: event.target.value, mediumPhotoValid: true, changed: true});
    }

    updateLargePrice(event) {
        this.setState({largePhoto: event.target.value, largePhotoValid: true, changed: true});
    }

    updateSuperLargePrice(event) {
        this.setState({superLargePhoto: event.target.value, superLargePhotoValid: true, changed: true});
    }

    save() {
        if (this.state.changed) {
            let standardPriceValid = false;
            let premiumPriceValid = false;
            let exclusivePriceValid = false;
            let smallPhotoValid = false;
            let mediumPhotoValid = false;
            let largePhotoValid = false;
            let superLargePhotoValid = false;
            let errors = [];

            if (priceRegex.test(this.state.standardPrice)) {
                standardPriceValid = true;
            } else {
                errors.push(<p>Please enter a valid price for the Standard Package.</p>);
            }

            if (priceRegex.test(this.state.premiumPrice)) {
                premiumPriceValid = true;
            } else {
                errors.push(<p>Please enter a valid price for the Premium Package.</p>);
            }

            if (priceRegex.test(this.state.exclusivePrice)) {
                exclusivePriceValid = true;
            } else {
                errors.push(<p>Please enter a valid price for the Exclusive Package.</p>);
            }

            if (priceRegex.test(this.state.smallPhoto)) {
                smallPhotoValid = true;
            } else {
                errors.push(<p>Please enter a valid price for the Small Photo Price.</p>);
            }

            if (priceRegex.test(this.state.mediumPhoto)) {
                mediumPhotoValid = true;
            } else {
                errors.push(<p>Please enter a valid price for the Medium Photo Price.</p>);
            }

            if (priceRegex.test(this.state.largePhoto)) {
                largePhotoValid = true;
            } else {
                errors.push(<p>Please enter a valid price for the Large Photo Price.</p>);
            }

            if (priceRegex.test(this.state.superLargePhoto)) {
                superLargePhotoValid = true;
            } else {
                errors.push(<p>Please enter a valid price for the X-Large Photo Price.</p>);
            }

            if (standardPriceValid && premiumPriceValid && exclusivePriceValid && smallPhotoValid && mediumPhotoValid && largePhotoValid && superLargePhotoValid) {
                axios.post('/photoprices', {
                    photo_prices: [this.state.smallPhoto, this.state.mediumPhoto, this.state.largePhoto, this.state.superLargePhoto]
                }).then((res) => {
                    this.setState({success: "Successfully updated prices."})
                }).catch((err) => {
                    errors.push(<p>Error updating prices, please try again later.</p>);
                })
                axios.post('/packages/prices', {
                    package_prices: [this.state.standardPrice, this.state.premiumPrice, this.state.exclusivePrice]
                }).then((res) => {
                    this.setState({success: "Successfully updated prices."})
                }).catch((err) => {
                    if (errors.length === 0) {
                        errors.push(<p>Error updating prices, please try again later.</p>);
                    }
                })
            }
            this.setState({errors, smallPhotoValid, mediumPhotoValid, largePhotoValid, standardPriceValid, premiumPriceValid, exclusivePriceValid})
        }
    }

    render() {
        return (
            <div className="User">
                <div className="info">
                    <div className="basic">
                        <TextField type="number" error={!this.state.standardPriceValid} value={this.state.standardPrice} onChange={this.updateStandardPrice} fontWeight={600} label="Standard Package Price" variant="outlined" />
                        <TextField type="number" error={!this.state.premiumPriceValid} value={this.state.premiumPrice} onChange={this.updatePremiumPrice} fontWeight={600} label="Premium Package Price" variant="outlined" />
                        <TextField type="number" error={!this.state.exclusivePriceValid} value={this.state.exclusivePrice} onChange={this.updateExclusivePrice} fontWeight={600} label="Exclusive Package Price" variant="outlined" />
                    </div>
                    <hr />
                    <div className="confidential">
                        <TextField type="number" error={!this.state.smallPhotoValid} value={this.state.smallPhoto} onChange={this.updateSmallPrice} fontWeight={600} label="Small Photo Price" variant="outlined" />
                        <TextField type="number" error={!this.state.mediumPhotoValid} value={this.state.mediumPhoto} onChange={this.updateMediumPrice} fontWeight={600} label="Medium Photo Price" variant="outlined" />
                        <TextField type="number" error={!this.state.largePhotoValid} value={this.state.largePhoto} onChange={this.updateLargePrice} fontWeight={600} label="Large Photo Price" variant="outlined" />
                        <TextField type="number" error={!this.state.superLargePhotoValid} value={this.state.superLargePhoto} onChange={this.updateSuperLargePrice} fontWeight={600} label="X-Large Photo Price" variant="outlined" />
                    </div>
                </div>
                <Button variant="contained" color="primary" onClick={this.save}>Save</Button>
                {this.state.errors.length > 0 && <div className="errors">{this.state.errors}</div>}
                {this.state.success.length > 0 && <div className="success">{this.state.success}</div>}
            </div>    
        )
    }
}

export default Edit
