import React, { Component } from 'react'

import nobook from '../../media/nobook.png'

const axios = require('axios');

export class PastBookings extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            bookings: [],
            rawBookings: [],
        }

        if (typeof window !== 'undefined' && !this.state.loaded) {
            if (!document.querySelector('#google-maps1')) {
                this.loadScript(
                'https://maps.googleapis.com/maps/api/js?key=AIzaSyCxyLmUgesPbcxC2g5fj0yqjlhAFVxCdTQ&libraries=places',
                document.querySelector('head'),
                'google-maps1',
                );
            }
            this.setState({loaded: true})
        }

        this.getFormattedDate = this.getFormattedDate.bind(this);
    }

    loadScript(src, position, id) {
        if (!position) {
          return;
        }

        const script = document.createElement('script');
        script.setAttribute('async', '');
        script.setAttribute('id', id);
        script.src = src;
        position.appendChild(script);
    }

    componentDidMount() {
        this.reRender();
    }

    getFormattedDate(date) {
        var year = date.getFullYear();
        
        var month = (1 + date.getMonth()).toString();
        month = month.length > 1 ? month : '0' + month;
        
        var day = date.getDate().toString();
        day = day.length > 1 ? day : '0' + day;
        
        var time = '';
        if (parseInt(date.toISOString().split(' ')[0].substring(11, 13)) > 12) {
            if (parseInt(date.toISOString().split(' ')[0].substring(11, 13)) - 12 > 9) {
                time = ((parseInt(date.toISOString().split(' ')[0].substring(11, 13)) - 12) + date.toISOString().split(' ')[0].substring(14, 16) + 'PM'); 
            } else {
                time = ('0' + (parseInt(date.toISOString().split(' ')[0].substring(11, 13)) - 12) + date.toISOString().split(' ')[0].substring(13, 16) + 'PM'); 
            }
        } else {
            time = (date.toISOString().split(' ')[0].substring(11, 16) + 'AM');
        }

        return month + '/' + day + '/' + year + ' ' + time;
    }

    reRender() {
        axios.get('/pastbookings')
            .then(res => {
                if (res.status === 200 || res.status === 304) {
                    res.data.sort((a, b) => {
                        if (new Date(a.date) < new Date(b.date)) {
                            return -1;
                        } else {
                            return 1; 
                        }
                    })

                    const bookingsMapped = res.data.map(booking => {
                        var urlLocation = booking.location.replace(" ", "+");
                        const staticMapLink = "https://maps.googleapis.com/maps/api/staticmap?&maptype=hybrid&size=600x400&markers=color:red%7C" + urlLocation + "&zoom=18&key=AIzaSyAXnO5_29oeGqNIchVstCYS29QCI-wBJ0Q";
                        return (
                        <div className="indivBooking" key={booking._id}>
                            <div className="topRow">
                                <p>{booking.first_name} {booking.last_name}, {booking.email}</p>
                            </div>
                            <p><span>Phone # : </span>{booking.phone_num}</p>
                            <p><span>Date: </span>{this.getFormattedDate(new Date(booking.date))}</p>
                            <p><span>Location: </span>{booking.location}</p>
                            <p><span>Type: </span>{booking.type}</p>
                            <p><span>Package:</span> {booking.package}</p>
                            <p className="notes"><span>Notes: </span>{booking.notes.length === 0 ? "[None]" : booking.notes}</p>
                            <a href={`https://www.google.com/maps/place/${urlLocation}`} target="_blank" rel="noreferrer"><img src={staticMapLink} className="map" alt="map"/></a>
                        </div>
                        );
                    })
                    
                    this.setState({rawBookings: res.data, bookings: bookingsMapped, confirmDelete: false})
                }
            }).catch(err => {})
    }

    render() {
        return (
            <div className="CurrentBookings Booked">
                {this.state.bookings.length > 0 ? this.state.bookings : <div className="emptyPanel"><img className="emptyPanel" src={nobook} alt="blank clipboard" /><h2>No Bookings.</h2></div>}
            </div>
        )
    }
}

export default PastBookings
