import React, { Component } from 'react'
import {Helmet} from 'react-helmet';
import {withRouter} from "react-router-dom";

import { Button, Paper, TextField, InputLabel, MenuItem, Select, FormControl, Grid, Typography} from '@material-ui/core';
import { KeyboardDatePicker, validate } from "@material-ui/pickers";
import Autocomplete from '@material-ui/lab/Autocomplete';
import LocationOnIcon from '@material-ui/icons/LocationOn';

import parse from 'autosuggest-highlight/parse';
import throttle from 'lodash/throttle';

import DelayLink from './DelayLink';

const axios = require('axios');

const emailRegex = /^(([^<>()[\].,;:\s@"]+(.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;
const nameRegex = /^[ a-zA-Z\-’]+$/;
const phoneRegex = /^[\d\-() ]{0,25}$/;
const phoneFinalRegex = /^[0-9]{10,11}$/;
const CVCRegex = /^[0-9]{3,4}$/;
const timeShift = new Date().getTimezoneOffset() / 60;
let autocompleteService;

const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
});

export class Bookings extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            loggedIn: true,
            firstName: '',
            lastName: '',
            email: '',
            phone: '',
            location: '',
            date: new Date().getHours() < 17 ? new Date(Date.now()+2*24*60*60*1000-timeShift*60*60*1000).toISOString() : new Date(Date.now()+3*24*60*60*1000-timeShift*60*60*1000).toISOString(),
            unavailableDates: [],
            timeDisabled: [false, false, false],
            time: '',
            type: '',
            package: this.props.package,
            notes: '',
            loaded: false,
            locationOptions: [],

            firstCardName: '',
            lastCardName: '',
            creditDate: new Date(),
            creditCard: '',
            CVC: '',

            firstNameValid: true,
            lastNameValid: true,
            phoneValid: true,
            emailValid: true,
            locationValid: true,
            dateValid: true,
            timeValid: true,
            typeValid: true,
            packageValid: true,

            firstCardNameValid: true,
            lastCardNameValid: true,
            creditDateValid: true,
            creditCardValid: true,
            CVCValid: true,

            errors: [],
            success: '',

            paymentPanel: false,
            prices: [],
            wentBack: 0,
        }

        if (typeof window !== 'undefined' && !this.state.loaded) {
            if (!document.querySelector('#google-maps')) {
                this.loadScript(
                'https://maps.googleapis.com/maps/api/js?key=AIzaSyCxyLmUgesPbcxC2g5fj0yqjlhAFVxCdTQ&libraries=places',
                document.querySelector('head'),
                'google-maps',
                );
            }
            this.setState({loaded: true})
        }

        this.updateEmail = this.updateEmail.bind(this);
        this.updateFirstName = this.updateFirstName.bind(this);
        this.updateLastName = this.updateLastName.bind(this);
        this.updatePhone = this.updatePhone.bind(this);
        this.updateLocationOptions = this.updateLocationOptions.bind(this);
        this.updateLocationOptions = throttle(this.updateLocationOptions, 500);
        this.updateLocation = this.updateLocation.bind(this);
        this.updateDate = this.updateDate.bind(this);
        this.updateTime = this.updateTime.bind(this);
        this.updateType = this.updateType.bind(this);
        this.updatePackage = this.updatePackage.bind(this);
        this.updateNotes = this.updateNotes.bind(this);

        this.updateFirstCardName = this.updateFirstCardName.bind(this);
        this.updateLastCardName = this.updateLastCardName.bind(this);
        this.updateCreditDate = this.updateCreditDate.bind(this);
        this.updateCreditCard = this.updateCreditCard.bind(this);
        this.updateCVC = this.updateCVC.bind(this);

        this.disableDates = this.disableDates.bind(this);
        this.proceedToPayment = this.proceedToPayment.bind(this);
        this.book = this.book.bind(this);
        this.back = this.back.bind(this);
    }

    loadScript(src, position, id) {
        if (!position) {
          return;
        }
      
        const script = document.createElement('script');
        script.setAttribute('async', '');
        script.setAttribute('id', id);
        script.src = src;
        position.appendChild(script);
    }

    componentDidMount() {
        setTimeout(() => autocompleteService = {current: new window.google.maps.places.AutocompleteService()}, 1500);
        axios.get('/booking').then(res => {
            let preUnavailableDates = [];
            preUnavailableDates = res.data.map(date => {
                return [date.slice(0,10), date.slice(11, 16), date];
            })
            if (new Date().getHours() >= 9 && new Date().getHours() < 17) {
                const getRidOfTime = new Date(new Date().getTime() + 1000*60*60*24*2).toISOString().slice(0,11) + '09:00:00.000Z';
                preUnavailableDates.push([getRidOfTime.slice(0,10), getRidOfTime.slice(11,16), getRidOfTime]);
            }
            if (new Date().getHours() >= 13 && new Date().getHours() < 17) {
                const getRidOfTime = new Date(new Date().getTime() + 1000*60*60*24*2).toISOString().slice(0,11) + '13:00:00.000Z';
                preUnavailableDates.push([getRidOfTime.slice(0,10), getRidOfTime.slice(11,16), getRidOfTime]);
            }
            this.setState({unavailableDates: preUnavailableDates}, () => {
                let combined = this.state.unavailableDates.map(pair => pair[2]);
                this.disableDates(new Date(this.state.date));
                this.setState({timeDisabled: [(combined.includes(new Date(this.state.date).toISOString().slice(0,11) + '09:00:00.000Z') || (combined.includes(new Date(new Date(this.state.date).getTime() - 1000*60*60*24).toISOString().slice(0,11) + '09:00:00.000Z') && new Date().getHours() >= 9 && new Date().getHours() < 17)),
                                            (combined.includes(new Date(this.state.date).toISOString().slice(0,11) + '13:00:00.000Z')) || (combined.includes(new Date(new Date(this.state.date).getTime() - 1000*60*60*24).toISOString().slice(0,11) + '13:00:00.000Z') && new Date().getHours() >= 13 && new Date().getHours() < 17), 
                                            (combined.includes(new Date(this.state.date).toISOString().slice(0,11) + '17:00:00.000Z'))]});
            });
        }).catch(err => {
        })
        this.props.checkLoggedIn().then((res) => {
            if (res !== undefined) {
                this.setState({firstName: res.first_name, firstCardName: res.first_name, lastName: res.last_name, lastCardName: res.last_name, email: res.email, phone: res.phone_num !== undefined ? res.phone_num + '' : '', loggedIn: true})
            } else {
                this.setState({loggedIn: false})
            }
        });
        axios.get('/edits').then(res => {
            this.setState({
                prices: res.data.package_prices
            })
        }).catch(err => {
        })
    }

    disableDates(day) {
        let dates = this.state.unavailableDates.map(pair => pair[0])
        let combined = this.state.unavailableDates.map(pair => pair[2])
        if (this.state.unavailableDates !== undefined && dates.includes(day.toISOString().slice(0,10))) {
            if ((combined.includes(day.toISOString().slice(0,11) + '09:00:00.000Z')) && (combined.includes(day.toISOString().slice(0,11) + '13:00:00.000Z')) && (combined.includes(day.toISOString().slice(0,11) + '17:00:00.000Z'))) {
                if (new Date(this.state.date).toISOString().slice(0,10) === day.toISOString().slice(0,10)) {
                    this.setState(prevState => {return {date: new Date(new Date(prevState.date).getTime() + 1000*60*60*24)}}, () => {
                        this.setState({timeDisabled: [(combined.includes(new Date(this.state.date).toISOString().slice(0,11) + '09:00:00.000Z')), (combined.includes(new Date(this.state.date).toISOString().slice(0,11) + '13:00:00.000Z')), (combined.includes(new Date(this.state.date).toISOString().slice(0,11) + '17:00:00.000Z'))]});
                    })
                }
                return true;
            }
        } else {
            return false;
        }
    }

    updateEmail(event) {
        this.setState({email: event.target.value, emailValid: true});
    }
    updateFirstName(event) {
       (event.target.value === '' || event.target.value.length === 0 || nameRegex.test(event.target.value)) && this.setState({firstName: event.target.value, firstNameValid: true});
    }

    updateLastName(event) {
        (event.target.value === '' || event.target.value.length === 0 || nameRegex.test(event.target.value)) && this.setState({lastName: event.target.value, lastNameValid: true});
    }

    updateFirstCardName(event) {
        (event.target.value === '' || event.target.value.length === 0 || nameRegex.test(event.target.value)) && this.setState({firstCardName: event.target.value, firstCardNameValid: true});
    }

    updateLastCardName(event) {
        (event.target.value === '' || event.target.value.length === 0 || nameRegex.test(event.target.value)) && this.setState({lastCardName: event.target.value, lastCardNameValid: true});
    }

    updatePhone(event) {
        const value = event.target.value;
        if ((phoneRegex.test(value) && value.split(" ").length - 1 <= 4) || value.length === 0) {
            this.setState({phone: value, phoneValid: true});  
        }
    }

    updateCreditCard(event) {
        if (event.target.value.length < 30 && !isNaN(event.target.value)) {
            this.setState({creditCard: event.target.value, creditCardValid: true});  
        }
    }

    updateCVC(event) {
        if (event.target.value.length < 5 && !isNaN(event.target.value)) {
            this.setState({CVC: event.target.value, CVCValid: true});
        }
    }

    validateCreditCard(value) {
        if (!isNaN(value)) {
            var visaRegEx = /^(?:4[0-9]{12}(?:[0-9]{3})?)$/;
            var mastercardRegEx = /^(?:5[1-5][0-9]{14})$/;
            var amexpRegEx = /^(?:3[47][0-9]{13})$/;
            var discovRegEx = /^(?:6(?:011|5[0-9][0-9])[0-9]{12})$/;
            var isValid = false;

            if (visaRegEx.test(value)) {
                isValid = true;
            } else if(mastercardRegEx.test(value)) {
                isValid = true;
            } else if(amexpRegEx.test(value)) {
                isValid = true;
            } else if(discovRegEx.test(value)) {
                isValid = true;
            }
            return isValid;
        } else {
            return false;
        }
    }

    updateLocation(place) {
        this.setState({location: place, locationValid: true});
    }

    updateDate(date) {
        if (date !== null && date._isValid) {
            let combined = this.state.unavailableDates.map(pair => pair[2]);
            this.setState({date: date.toISOString(), dateValid: true, timeDisabled: [(combined.includes(date.toISOString().slice(0,11) + '09:00:00.000Z')), (combined.includes(date.toISOString().slice(0,11) + '13:00:00.000Z')), (combined.includes(date.toISOString().slice(0,11) + '17:00:00.000Z'))]});
        } else {
            this.setState({date: '', dateValid: false})
        }
    }

    updateCreditDate(creditDate) {
        if (creditDate !== null && creditDate._isValid) {
            this.setState({creditDate: creditDate.toISOString(), creditDateValid: true});
        } else {
            this.setState({creditDate: '', creditDateValid: false})
        }
    }
    
    updateTime(event) {
        this.setState({time: event.target.value, timeValid: true})
    }

    updateType(event) {
        this.setState({type: event.target.value, typeValid: true})
    }

    updatePackage(event) {
        this.setState({package: event.target.value, packageValid: true})
    }

    updateNotes(event) {
        this.setState({notes: event.target.value})
    }

    updateLocationOptions(event, clear) {
        if (clear) {
            this.setState({location: '', locationOptions: []})
        } else {
            if (!autocompleteService.current && window.google) {
                autocompleteService.current = new window.google.maps.places.AutocompleteService();
            }
            if (event !== null && event.target.value !== '' ) {
                autocompleteService.current.getPlacePredictions({input: event.target.value, types: ['geocode'], country: ['us']}, (outputs) => {
                    this.setState({locationOptions: outputs})  
                })
            } else {
                this.setState({locationOptions: []})
            }
        }
    }

    back() {
        this.props.runLoader(750);
        setTimeout(() => this.setState({paymentPanel: false, errors: ''}), 450);
    }

    book() {
        let firstCardNameValid = false;
        let lastCardNameValid = false;
        let creditDateValid = false;
        let creditCardValid = false;
        let CVCValid = false;
        let errors = [];
        
        if (this.state.firstCardName.length > 1) {
            firstCardNameValid = true;
        } else {
            errors.push(<p>Please enter the first name on the credit card.</p>);
        }

        if (this.state.lastCardName.length > 1) {
            lastCardNameValid = true;
        } else {
            errors.push(<p>Please enter the last name on the credit card.</p>);
        }

        if (this.state.creditDate !== null && new Date(this.state.creditDate).toISOString().length > 0) {
            creditDateValid = true;
        } else {
            errors.push(<p>Please enter the expiry date of the credit card.</p>);
        }

        if (this.validateCreditCard(this.state.creditCard)) {
            creditCardValid = true;
        } else {
            errors.push(<p>Please enter a valid credit card.</p>);
        }

        if (CVCRegex.test(this.state.CVC)) {
            CVCValid = true;
        } else {
            errors.push(<p>Please enter a valid CVC.</p>);
        }

        if (firstCardNameValid && lastCardNameValid && creditDateValid && creditCardValid && CVCValid) {
            this.setState({success: 'Loading...'});
            axios.post('/bookings', {
                first_name: this.state.firstName,
                last_name: this.state.lastName,
                email: this.state.email,
                phone_num: this.state.phone,
                location: this.state.location.description,
                date: (new Date(this.state.date).toISOString().slice(0,11) + this.state.time + '.000Z'),
                type: this.state.type,
                package: this.state.package,
                notes: this.state.notes,
            }).then(res => {
                if (res.status === 200 || res.status === 304) {
                    axios.get('/booking').then(res => {
                        let preUnavailableDates = [];
                        preUnavailableDates = res.data.map(date => {
                            return [date.slice(0,10), date.slice(11, 16), date];
                        })
                        if (new Date().getHours() >= 9 && new Date().getHours() < 17) {
                            const getRidOfTime = new Date(new Date().getTime() + 1000*60*60*24*2).toISOString().slice(0,11) + '09:00:00.000Z';
                            preUnavailableDates.push([getRidOfTime.slice(0,10), getRidOfTime.slice(11,16), getRidOfTime]);
                        }
                        if (new Date().getHours() >= 13 && new Date().getHours() < 17) {
                            const getRidOfTime = new Date(new Date().getTime() + 1000*60*60*24*2).toISOString().slice(0,11) + '13:00:00.000Z';
                            preUnavailableDates.push([getRidOfTime.slice(0,10), getRidOfTime.slice(11,16), getRidOfTime]);
                        }
                        this.setState({unavailableDates: preUnavailableDates}, () => {
                            let combined = this.state.unavailableDates.map(pair => pair[2]);
                            this.disableDates(new Date(this.state.date));
                            this.setState({timeDisabled: [(combined.includes(new Date(this.state.date).toISOString().slice(0,11) + '09:00:00.000Z')), (combined.includes(new Date(this.state.date).toISOString().slice(0,11) + '13:00:00.000Z')), (combined.includes(new Date(this.state.date).toISOString().slice(0,11) + '17:00:00.000Z'))]});
                            this.setState({errors: [],
                                firstCardNameValid, lastCardNameValid, creditDateValid, creditCardValid, CVCValid, 
                                emailValid: true, firstNameValid: true, lastNameValid: true, phoneValid: true, locationValid: true, dateValid: true, timeValid: true, typeValid: true, packageValid: true, success: 'Successfully booked! Check your email for confirmation. This is a demonstration site, so the credit card information is ignored.', 
                                locationOptions: [],
                                location: '',
                                time: '',
                                type: '',
                                package: '',
                                notes: '',
                                firstCardName: '',
                                lastCardName: '',
                                creditDate: new Date(),
                                creditCard: '',
                                CVC: '',
                            })
                            this.back();
                        });
                    }).catch(err => {
                    })
                } else {
                    const errors = [<p>Error booking, try again later.</p>]
                    this.setState({errors: errors, firstCardNameValid, lastCardNameValid, creditDateValid, creditCardValid, CVCValid, 
                        emailValid: true, firstNameValid: true, lastNameValid: true, phoneValid: true, locationValid: true, dateValid: true, timeValid: true, typeValid: true, packageValid: true, success: ''})
                }
            }).catch(err => {
                const errors = [<p>Error booking, try again later.</p>]
                this.setState({errors: errors, firstCardNameValid, lastCardNameValid, creditDateValid, creditCardValid, CVCValid, 
                    emailValid: true, firstNameValid: true, lastNameValid: true, phoneValid: true, locationValid: true, dateValid: true, timeValid: true, typeValid: true, packageValid: true, success: ''})
            })
        } else {
            this.setState({errors: errors, firstCardNameValid, lastCardNameValid, creditDateValid, creditCardValid, CVCValid, success: ''})
        }
    }

    proceedToPayment() {
        let firstNameValid = false;
        let lastNameValid = false;
        let phoneValid = false;
        let emailValid = false;
        let locationValid = false;
        let dateValid = false;
        let timeValid = false;
        let typeValid = false;
        let packageValid = false;
        let errors = [];

        if (this.state.firstName.length > 1) {
            firstNameValid = true;
        } else {
            errors.push(<p>Please enter a first name.</p>);
        }

        if (this.state.lastName.length > 1) {
            lastNameValid = true;
        } else {
            errors.push(<p>Please enter a last name.</p>);
        }

        if (emailRegex.test(this.state.email)) {
            emailValid = true;
        } else {
            errors.push(<p>Please enter a valid email.</p>);
        }

        if (phoneRegex.test(this.state.phone) && this.state.phone.split(" ").length - 1 <= 4 && this.state.phone.length > 0 && phoneFinalRegex.test(this.state.phone.replace(/-/g, "").replace(/[()]/g, "").replace(/\s/g, ""))) {
            phoneValid = true;  
        } else {
            errors.push(<p>Please enter a valid phone number.</p>);
        }

        if (this.state.date !== null && new Date(this.state.date).toISOString().length > 0) {
            dateValid = true;
        } else {
            errors.push(<p>Please enter a date.</p>);
        }

        if (this.state.location.description !== undefined && this.state.location.description !== null && this.state.location.description.length > 0) {
            locationValid = true;
        } else {
            errors.push(<p>Please enter an address.</p>);
        }

        if (this.state.time !== null && this.state.time.length > 0) {
            timeValid = true;
        } else {
            errors.push(<p>Please select a time.</p>);
        }

        if (this.state.type !== null && this.state.type.length > 0) {
            typeValid = true;
        } else {
            errors.push(<p>Please select a category.</p>);
        }

        if (this.state.package !== null && this.state.package.length > 0) {
            packageValid = true;
        } else {
            errors.push(<p>Please select a package.</p>);
        }

        if (emailValid && firstNameValid && lastNameValid && phoneValid && locationValid && dateValid && timeValid && typeValid && packageValid) {
            this.props.runLoader(750);
            setTimeout(() => this.setState({wentBack: Math.random() * 10000, paymentPanel: true, errors: errors, emailValid, firstNameValid, lastNameValid, phoneValid, locationValid, dateValid, timeValid, typeValid, packageValid, success: ''}), 450);
        } else {
            this.setState({errors: errors, emailValid, firstNameValid, lastNameValid, phoneValid, locationValid, dateValid, timeValid, typeValid, packageValid, success: ''})
        }
    }

    render() {
        return (
                <div className="Login Book">
                    <Helmet>
                        <title>{!this.state.paymentPanel ? "Create a Booking" : "Booking Payment Method"} - Omar Hill Photography</title>
                    </Helmet>
                    <h1 className="title">{!this.state.paymentPanel ? "Create a Booking" : "Booking Payment Method"}</h1>
                        {!this.state.paymentPanel ?
                            <Paper className="container" elevation={5}>
                                {this.state.errors.length > 0 && <div className="errors">{this.state.errors}</div>}
                                <div className="input">
                                        <TextField className="name" error={!this.state.firstNameValid} value={this.state.firstName} onChange={this.updateFirstName} fontWeight={600} label="First Name" variant="outlined" />
                                        <TextField className="name" error={!this.state.lastNameValid} value={this.state.lastName} onChange={this.updateLastName} fontWeight={600} label="Last Name" variant="outlined" />
                                        <TextField className="email" error={!this.state.emailValid} value={this.state.email} onChange={this.updateEmail} fontWeight={600} label="Email" type="email" variant="outlined" />
                                        <TextField className="phone" error={!this.state.phoneValid} value={this.state.phone} onChange={this.updatePhone} fontWeight={600} label="Phone Number" type="tel" variant="outlined"/>
                                        <KeyboardDatePicker
                                            className="date"
                                            error={!this.state.dateValid}
                                            autoOk
                                            variant="inline"
                                            inputVariant="outlined"
                                            label="Select Date"
                                            format="MM/DD/yyyy"
                                            minDate={new Date().getHours() < 17 ? new Date(Date.now()+2*24*60*60*1000).toISOString() : new Date(Date.now()+3*24*60*60*1000).toISOString()}
                                            maxDate={new Date(Date.now()+365*24*60*60*1000)}
                                            allowKeyboardControl
                                            shouldDisableDate={(day) => this.disableDates(day)}
                                            value={new Date(new Date(new Date(this.state.date).toISOString().slice(0,11)+'00:00:00.000Z').getTime()+60*60*timeShift*1000)}
                                            onChange={date => this.updateDate(date)}
                                        />
                                        <FormControl variant="outlined" className="dropdown" error={!this.state.timeValid}>
                                            <InputLabel>Select Time</InputLabel>
                                            <Select label="Select Time" value={this.state.time} onChange={this.updateTime}>
                                                <MenuItem disabled={this.state.timeDisabled[0]} value={'09:00:00'}>9:00 AM</MenuItem>
                                                <MenuItem disabled={this.state.timeDisabled[1]} value={'13:00:00'}>1:00 PM</MenuItem>
                                                <MenuItem disabled={this.state.timeDisabled[2]} value={'17:00:00'}>5:00 PM</MenuItem>
                                            </Select>
                                        </FormControl>
                                        {this.state.wentBack === 0 ?
                                        <Autocomplete
                                            autoComplete
                                            autoHighlight
                                            id="google-maps"
                                            options={this.state.locationOptions === null ? [] : this.state.locationOptions}
                                            getOptionLabel={(option) => (option.description)}
                                            className="location"
                                            includeInputInList
                                            renderInput={(params) => (<TextField {...params} label="Address" variant="outlined" fullWidth error={!this.state.locationValid}/>)}
                                            onInputChange={(event) => this.updateLocationOptions(event, false)}
                                            onChange={(event, place) => this.updateLocation(place)}
                                            renderOption={(option) => {
                                                if (option !== null && option !== undefined) {
                                                    const matches = option.structured_formatting.main_text_matched_substrings;
                                                    const parts = parse(
                                                    option.structured_formatting.main_text,
                                                    matches.map((match) => [match.offset, match.offset + match.length]),
                                                    );
                                            
                                                    return (
                                                    <Grid container alignItems="center">
                                                        <Grid item>
                                                        <LocationOnIcon />
                                                        </Grid>
                                                        <Grid item xs>
                                                        {parts.map((part, index) => (
                                                            <span key={index} style={{ fontWeight: part.highlight ? 700 : 400 }}>
                                                            {part.text}
                                                            </span>
                                                        ))}
                                            
                                                        <Typography variant="body2" color="textSecondary">
                                                            {option.structured_formatting.secondary_text === undefined ? "" : option.structured_formatting.secondary_text.substring(0, option.structured_formatting.secondary_text.length-5)}
                                                        </Typography>
                                                        </Grid>
                                                    </Grid>
                                                    );
                                                }
                                            }}
                                        /> : 
                                        <Autocomplete
                                            key={this.state.wentBack}
                                            autoComplete
                                            autoHighlight
                                            id="google-maps"
                                            options={this.state.locationOptions === null ? [] : this.state.locationOptions}
                                            getOptionLabel={(option) => (option.description)}
                                            className="location"
                                            includeInputInList
                                            renderInput={(params) => (<TextField {...params} label="Address" variant="outlined" fullWidth error={!this.state.locationValid}/>)}
                                            onInputChange={(event) => this.updateLocationOptions(event, false)}
                                            onChange={(event, place) => this.updateLocation(place)}
                                            defaultValue={this.state.location}
                                            renderOption={(option) => {
                                                if (option !== null && option !== undefined) {
                                                    const matches = option.structured_formatting.main_text_matched_substrings;
                                                    const parts = parse(
                                                    option.structured_formatting.main_text,
                                                    matches.map((match) => [match.offset, match.offset + match.length]),
                                                    );
                                            
                                                    return (
                                                    <Grid container alignItems="center">
                                                        <Grid item>
                                                        <LocationOnIcon />
                                                        </Grid>
                                                        <Grid item xs>
                                                        {parts.map((part, index) => (
                                                            <span key={index} style={{ fontWeight: part.highlight ? 700 : 400 }}>
                                                            {part.text}
                                                            </span>
                                                        ))}
                                            
                                                        <Typography variant="body2" color="textSecondary">
                                                            {option.structured_formatting.secondary_text === undefined ? "" : option.structured_formatting.secondary_text.substring(0, option.structured_formatting.secondary_text.length-5)}
                                                        </Typography>
                                                        </Grid>
                                                    </Grid>
                                                    );
                                                }
                                            }}
                                        />
                                        }
                                        <FormControl variant="outlined" className="dropdown" error={!this.state.typeValid}>
                                            <InputLabel>Category</InputLabel>
                                            <Select label="Category" value={this.state.type} onChange={this.updateType}>
                                                <MenuItem value={'Weddings'}>Weddings</MenuItem>
                                                <MenuItem value={'Urban'}>Urban</MenuItem>
                                                <MenuItem value={'Modeling'}>Modeling</MenuItem>
                                                <MenuItem value={'Landscape'}>Landscape</MenuItem>
                                                <MenuItem value={'Graduations'}>Graduations</MenuItem>
                                                <MenuItem value={'Family Portraits'}>Family Portraits</MenuItem>
                                            </Select>
                                        </FormControl>
                                        <FormControl variant="outlined" className="dropdown" error={!this.state.packageValid}>
                                            <InputLabel>Package</InputLabel>
                                            <Select label="Package" value={this.state.package} onChange={this.updatePackage}>
                                                <MenuItem value={'Standard'}>Standard</MenuItem>
                                                <MenuItem value={'Premium'}>Premium</MenuItem>
                                                <MenuItem value={'Exclusive'}>Exclusive</MenuItem>
                                            </Select>
                                        </FormControl>
                                        <TextField
                                            className="notes"
                                            label="Additional Notes"
                                            multiline
                                            rows={3}
                                            variant="outlined"
                                            value={this.state.notes}
                                            onChange={this.updateNotes}
                                        />
                                </div>
                                <div className="BookButtons">
                                    <Button onClick={this.proceedToPayment} variant="contained" color="secondary">Proceed</Button>
                                    {!this.state.loggedIn ? <Button variant="contained" className="signup" color="primary"><DelayLink delay={500} to="/signup" onDelayStart={() => this.props.runLoader(750)}>Sign up to manage bookings</DelayLink></Button> :
                                    <Button variant="contained" className="signup" color="primary"><DelayLink delay={500} to="/dashboard/booked" onDelayStart={() => this.props.runLoader(750)}>View Current Bookings</DelayLink></Button>
                                    }
                                    <Button className="viewpkg" variant="contained"><DelayLink delay={600} to="/packages" onDelayStart={() => this.props.runLoader(750)}>View Packages</DelayLink></Button>
                                </div>
                                {this.state.success.length > 0 && <div className="success animate__animated animate_fadeIn">{this.state.success}</div>}
                            </Paper>
                        :
                            <Paper className="container" elevation={5}>
                                {this.state.errors.length > 0 && <div className="errors">{this.state.errors}</div>}
                                <div className="input">
                                    <p className="paymentInfo">Total Payment: {formatter.format(this.state.prices[this.state.package === 'Standard' ? 0 : ( this.state.package === 'Premium' ? 1 : 2)])} ({this.state.package} Package)</p>
                                    <TextField className="cardName" error={!this.state.firstCardNameValid} value={this.state.firstCardName} onChange={this.updateFirstCardName} fontWeight={600} label="First Name on Card" variant="outlined" />
                                    <TextField className="cardName" error={!this.state.lastCardNameValid} value={this.state.lastCardName} onChange={this.updateLastCardName} fontWeight={600} label="Last Name on Card" variant="outlined" />
                                    <KeyboardDatePicker
                                        className="expiryDate"
                                        error={!this.state.creditDateValid}
                                        autoOk
                                        variant="inline"
                                        inputVariant="outlined"
                                        label="Expiry Date"
                                        views={["month", "year"]}
                                        format="MM/yyyy"
                                        minDate={new Date(Date.now()-31*24*60*60*1000)}
                                        maxDate={new Date(Date.now()+10*365*24*60*60*1000)}
                                        allowKeyboardControl
                                        value={new Date((this.state.creditDate))}
                                        onChange={date => this.updateCreditDate(date)}
                                    />
                                    <TextField className="creditCard" error={!this.state.creditCardValid} value={this.state.creditCard} onChange={this.updateCreditCard} fontWeight={600} label="Credit Card Number" variant="outlined" />
                                    <TextField className="CVC" error={!this.state.CVCValid} value={this.state.CVC} onChange={this.updateCVC} fontWeight={600} label="CVC" variant="outlined" />
                                </div>
                                <div className="PaymentButtons">
                                    <Button onClick={this.book} variant="contained" color="secondary">Book Now</Button>
                                    <Button onClick={this.back} variant="contained" color="primary">Back</Button>
                                </div>
                                {this.state.success.length > 0 && <div className="success animate__animated animate_fadeIn">{this.state.success}</div>}
                            </Paper>
                        }
                </div>
        )
    }
}

export default withRouter(Bookings);