import React, { Component } from 'react'
import Helmet from 'react-helmet'
import { Link } from "react-router-dom";

import Booked from './Booked';
import Cart from './Cart';
import Favorites from './Favorites';
import User from './User';
import Edit from './Edit';
import CurrentBookings from './CurrentBookings';
import PastBookings from './PastBookings';

const axios = require('axios');

export class Dashboard extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            panel: this.props.panel,
            panelSelected: this.props.panel,
            user: undefined,
        }

        this.renderTab = this.renderTab.bind(this);
        this.switchTab = this.switchTab.bind(this);
    }

    componentDidMount() {
        performance.getEntriesByType("navigation")[0]["type"] === "back_forward" ? window.location.reload() :
        axios.get('/user')
        .then(res => {
            if (res.status === 200 || res.status === 304) {
                this.setState({user: res.status === 240 ? false: true})
            }
        })
        .catch(err => {})
    }

    switchTab(panel) {
        this.props.runLoader(750, false, 'dashboard');
        this.setState({panelSelected: panel});
        setTimeout(() => this.setState({panel: panel}), 350);
    }

    renderTab() {
        switch (this.state.panel) {
            default:
            case 'user': 
                return <User runLoader={this.props.runLoader} user={this.state.user} captions={this.props.captions}/>
            // user
            case 'booked':
                return <Booked runLoader={this.props.runLoader}/>;
            case 'favorites':
                return <Favorites runLoader={this.props.runLoader} captions={this.props.captions} user={this.state.user}/>;
            case 'cart':
                return <Cart runLoader={this.props.runLoader} captions={this.props.captions} user={this.state.user}/>
            // admin
            case 'edit':
                return <Edit runLoader={this.props.runLoader} user={this.state.user} captions={this.props.captions}/>
            case 'currentbookings':
                return <CurrentBookings runLoader={this.props.runLoader} user={this.state.user} captions={this.props.captions}/>
            case 'pastbookings':
                return <PastBookings runLoader={this.props.runLoader} user={this.state.user} captions={this.props.captions}/>
        }
    }

    render() {
        return (
            <div className="Dashboard">
                <Helmet>
                    <title>{this.props.admin ? "Admin Dashboard" : "Dashboard"} - Omar Hill Photography</title>
                </Helmet>
                {this.state.user ? 
                <div className="tabs">
                    <Link className={`tab leftmost ${this.state.panelSelected === 'booked' && 'selected'}`} to="/dashboard/booked" onClick={() => this.switchTab('booked')}>Bookings</Link>
                    <Link className={`tab ${this.state.panelSelected === 'favorites' && 'selected'}`} to="/dashboard/favorites" onClick={() => this.switchTab('favorites')}>Favorites</Link>
                    <Link className={`tab ${this.state.panelSelected === 'cart' && 'selected'}`} to="/dashboard/cart" onClick={() => this.switchTab('cart')}>Cart</Link>
                    <Link className={`tab rightmost ${this.state.panelSelected === 'user' && 'selected'}`} to="/dashboard/user" onClick={() => this.switchTab('user')}>User Settings</Link>
                </div>
                : 
                <div className="tabs">
                    <Link className={`tab leftmost ${this.state.panelSelected === 'edit' && 'selected'}`} to="/dashboard/edit" onClick={() => this.switchTab('edit')}>Edit Prices</Link>
                    <Link className={`tab ${this.state.panelSelected === 'currentbookings' && 'selected'}`} to="/dashboard/currentbookings" onClick={() => this.switchTab('currentbookings')}>Current Bookings</Link>
                    <Link className={`tab ${this.state.panelSelected === 'pastbookings' && 'selected'}`} to="/dashboard/pastbookings" onClick={() => this.switchTab('pastbookings')}>Past Bookings</Link>
                    <Link className={`tab rightmost ${this.state.panelSelected === 'user' && 'selected'}`} to="/dashboard/user" onClick={() => this.switchTab('user')}>Admin Settings</Link>
                </div>
                }
                <div className="tabpanel">
                    {this.renderTab()}
                </div>
            </div>
        )
    }
}

export default Dashboard
