import { React, Component } from 'react'

import { Paper, Button } from '@material-ui/core';

import Helmet from 'react-helmet';
import DelayLink from './DelayLink';

const axios = require('axios');

export class Packages extends Component {
    constructor(props) {
      super(props)
    
      this.state = {
          standardPrice: 0,
          premiumPrice: 0,
          exclusivePrice: 0
      }
    }

    componentDidMount() {
        axios.get('/edits').then((res) => {
            this.setState({
                standardPrice: res.data.package_prices[0],
                premiumPrice: res.data.package_prices[1],
                exclusivePrice: res.data.package_prices[2],
            })
        });
    }

    render() {
        return (
            <div className="Packages About">
                <Helmet>
                    <title>Packages - Omar Hill Photography</title>
                </Helmet>
                <h1 className="title">Packages</h1>
                <div className="packages">
                    <Paper elevation={24} className="package smallPackage leftPackage">
                        <h1>Standard</h1>
                        <h2>${this.state.standardPrice} <span>/ session</span></h2>
                        <hr></hr>
                        <ul>
                            <li>1 hour session</li>
                            <li>200 pictures</li>
                            <li>3 Framed Collages</li>
                        </ul>
                        <Button variant="contained" color="primary"><DelayLink delay={600} to="/bookings/standard" onDelayStart={() => this.props.runLoader(750)}>Book Now</DelayLink></Button> 
                    </Paper>
                    <Paper elevation={24} className="package mainPackage">
                        <h1>Premium</h1>
                        <h2>${this.state.premiumPrice} <span>/ session</span></h2>
                        <hr></hr>
                        <ul>
                            <li>1 hour and 30 minute session</li>
                            <li>400 pictures</li>
                            <li>5 Framed Collages</li>
                            <li>1 Oversized Canvas</li>
                        </ul>
                        <Button variant="contained" color="primary"><DelayLink delay={600} to="/bookings/premium" onDelayStart={() => this.props.runLoader(750)}>Book Now</DelayLink></Button> 
                        <div className="header">
                            <p>Most Popular</p>
                        </div>
                    </Paper>
                    <Paper elevation={24} className="package smallPackage rightPackage">
                        <h1>Exclusive</h1>
                        <h2>${this.state.exclusivePrice} <span>/ session</span></h2>
                        <hr></hr>
                        <ul>
                            <li>2 hour session</li>
                            <li>7 Framed Collages</li>
                            <li>2 Oversized Canvas</li>
                            <li>Custom Picture Book</li>
                        </ul>
                        <Button variant="contained" color="primary"><DelayLink delay={600} to="/bookings/exclusive" onDelayStart={() => this.props.runLoader(750)}>Book Now</DelayLink></Button> 
                    </Paper>
                </div>
            </div>
        )
    }
}

export default Packages
