const theme = createTheme({
  palette: {
    primary: {
      main: '#BC9D8E',
    },
    secondary: {
      main: '#373A36',
    },
    error: {
      main: '#cc0000',
    },
  },
  typography: {
    fontFamily: ['Source Sans']
  }
});

export class App extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
      nav: undefined,
      navInAnim: false,
      loader: false,
      loaderType: 'default',
      loggedIn: undefined,
      portfolio: this.props.portfolio,
      admin: undefined,
      captions: [],
    }

    this.toggleNav = this.toggleNav.bind(this);
    this.runLoader = this.runLoader.bind(this);
    this.checkLoggedIn = this.checkLoggedIn.bind(this);
    this.getCaptions = this.getCaptions.bind(this);
  }

  componentDidMount() {
    this.getCaptions((res) => {
      this.setState({captions: res.data})
    });
    this.checkLoggedIn();
    setTimeout(() => {
      this.setState({delay: false});
    }, 1000);
  }

  runLoader(time = 1000, toggle = false, type = 'default') {
    toggle !== false && this.toggleNav();
    setTimeout(() => {
      this.setState({loader: false})
    }, time);
    this.setState({loader: true, loaderType: type})
  }

  toggleNav() {
    setTimeout(() => {
      this.setState({navInAnim: false})
    }, 500);
    this.setState(prevState => {
      if (!prevState.navInAnim) {
        return {nav: !prevState.nav, navInAnim: true}
      }
    })
  }

  getCaptions(callback = () => {}, mask = false) {
    if (!mask) {
      axios.get('/pics').then(res => callback(res))
    }
  }

  checkLoggedIn() {
    return (
    axios.get('/user')
    .then(res => {
      if (res.status === 200 || res.status === 302 || res.status === 240) {
        this.setState({loggedIn: true, admin: res.status === 240})
        return res.data;
      } else {
        this.setState({loggedIn: false, admin: false})
      }
    }).catch(err => {
      if (err.response.status === 401) {
        this.setState({loggedIn: false, admin: false})
      }
    }));
  }

  render() {
    return (
        <Suspense fallback={<div></div>}>
        <Preloader fadeDuration={2900} style={{backgroundColor: '#373A36', transition: '1s', transitionDelay: '1.9s'}}>
          <Placeholder>
            <img src={loadingWEBP} alt="loading..."/>
          </Placeholder>
        </Preloader>
        <div className={`loader ${(this.state.loader ? 'animate__animated animate__fadeIn' : 'animate__animated animate__fadeOut gone')} ${this.state.loaderType === 'dashboard' && `loaderDashboard ${this.state.nav && 'shiftedDashboard'}`}`}>
            {this.state.loader && <img src={loadingLogo} alt="loading..."/>}
        </div>
        <ThemeProvider theme={theme}>
        <Router>
          <Nav nav={this.state.nav} toggleNav={this.toggleNav} runLoader={this.runLoader} loggedIn={this.state.loggedIn} checkLoggedIn={this.checkLoggedIn} admin={this.state.admin}/>
          <div className="nav-button">
            <div className="svg-conatiner">
              <svg className={`ham hamRotate ham8 ${this.state.nav ? 'active' : ''}`} viewBox="0 0 100 100" preserveAspectRatio="xMinYMin meet" onClick={this.toggleNav}>
                <path className="line top" d="m 30,33 h 40 c 3.722839,0 7.5,3.126468 7.5,8.578427 0,5.451959 -2.727029,8.421573 -7.5,8.421573 h -20" />
                <path className="line middle" d="m 30,50 h 40" />
                <path className="line bottom" d="m 70,67 h -40 c 0,0 -7.5,-0.802118 -7.5,-8.365747 0,-7.563629 7.5,-8.634253 7.5,-8.634253 h 20" />
              </svg>
            </div>
          </div>
          <div className={`App ${this.state.nav !== undefined && (!this.state.nav ? 'animate__animated animate__slideInRightNav' : 'animate__animated animate__slideOutRightNav')}`}>
            <Switch>
              <Route exact path="/" render={(props) => <Landing admin={this.state.admin} loggedIn={this.state.loggedIn} runLoader={this.runLoader} toggleNav={this.toggleNav} {...props}/>}/>
              {this.state.admin !== undefined && <Route path="/editStore">
                {this.state.admin ? 
                  <Portfolio loggedIn={this.state.loggedIn} runLoader={this.runLoader} getCaptions={this.getCaptions} admin={this.state.admin}/>
                : <Redirect to="/store"/>}
              </Route> 
                }
                {// Not logged in routes
              this.state.loggedIn !== undefined && <Route path="/login">
                {this.state.loggedIn ? <Redirect to="/dashboard"/> : <Login key={'login'} panel='login' runLoader={this.runLoader} checkLoggedIn={this.checkLoggedIn}/>}
              </Route> }
              {this.state.loggedIn !== undefined && <Route path="/signup">
                {this.state.loggedIn ? <Redirect to="/dashboard"/> : <Login key={'signup'} panel='signup' runLoader={this.runLoader} />}
              </Route> }
              {this.state.loggedIn !== undefined && <Route path="/forgot">
                {this.state.loggedIn ? <Redirect to="/dashboard"/> : <Login key={'forgot'} panel='forgot' runLoader={this.runLoader} />}
              </Route> }
              {// User routes 
              this.state.loggedIn !== undefined && !this.state.admin && <Route path="/dashboard/booked">
                {this.state.loggedIn ? <Dashboard admin={this.state.admin} panel='booked' runLoader={this.runLoader} captions={this.state.captions}/> : <Redirect to="/login" />}
              </Route> }
              {this.state.loggedIn !== undefined && !this.state.admin && <Route path="/dashboard/favorites">
                {this.state.loggedIn ? <Dashboard admin={this.state.admin} panel='favorites' runLoader={this.runLoader} captions={this.state.captions}/> : <Redirect to="/login" />}
              </Route> }
              {this.state.loggedIn !== undefined && !this.state.admin && <Route path="/dashboard/cart">
                {this.state.loggedIn ? <Dashboard admin={this.state.admin} panel='cart' runLoader={this.runLoader} captions={this.state.captions}/> : <Redirect to="/login" />}
              </Route> }
              {this.state.loggedIn !== undefined && !this.state.admin && <Route path="/dashboard/user">
                {this.state.loggedIn ? <Dashboard admin={this.state.admin} panel='user' runLoader={this.runLoader} captions={this.state.captions}/> : <Redirect to="/login" />}
              </Route> }
              {this.state.loggedIn !== undefined && !this.state.admin && <Route path="/dashboard">
                {this.state.loggedIn ? <Redirect to="/dashboard/booked"/> : <Redirect to="/login" />}
              </Route> }
            {// Admin routes
              this.state.loggedIn !== undefined && this.state.admin && <Route path="/dashboard/edit">
              {this.state.loggedIn ? <Dashboard admin={this.state.admin} panel='edit' runLoader={this.runLoader} captions={this.state.captions}/> : <Redirect to="/login" />}
            </Route> }
            {this.state.loggedIn !== undefined && this.state.admin && <Route path="/dashboard/currentbookings">
              {this.state.loggedIn ? <Dashboard admin={this.state.admin} panel='currentbookings' runLoader={this.runLoader} captions={this.state.captions}/> : <Redirect to="/login" />}
            </Route> }
            {this.state.loggedIn !== undefined && this.state.admin && <Route path="/dashboard/pastbookings">
              {this.state.loggedIn ? <Dashboard admin={this.state.admin} panel='pastbookings' runLoader={this.runLoader} captions={this.state.captions}/> : <Redirect to="/login" />}
            </Route> }
            {this.state.loggedIn !== undefined && this.state.admin && <Route path="/dashboard/user">
              {this.state.loggedIn ? <Dashboard admin={this.state.admin} panel='user' runLoader={this.runLoader} captions={this.state.captions}/> : <Redirect to="/login" />}
            </Route> }
            {this.state.loggedIn !== undefined && this.state.admin && <Route path="/dashboard">
              {this.state.loggedIn ? <Redirect to="/dashboard/edit"/> : <Redirect to="/login" />}
            </Route> }
              <Route path="/bookings/standard" render={(props) => <Bookings key={'standard'} package='Standard' runLoader={this.runLoader} checkLoggedIn={this.checkLoggedIn} {...props}/>}>
                {this.state.admin && <Redirect to="/packages" />}
              </Route>
              <Route path="/bookings/premium" render={(props) => <Bookings key={'premium'} package='Premium' runLoader={this.runLoader} checkLoggedIn={this.checkLoggedIn} {...props}/>}>
                {this.state.admin && <Redirect to="/packages" />}
              </Route>
              <Route path="/bookings/exclusive" render={(props) => <Bookings key={'exclusive'} package='Exclusive' runLoader={this.runLoader} checkLoggedIn={this.checkLoggedIn} {...props}/>}>
                {this.state.admin && <Redirect to="/packages" />}
              </Route>
              <Route path="/bookings" render={(props) => <Bookings key={'none'} package='' runLoader={this.runLoader} checkLoggedIn={this.checkLoggedIn} {...props}/>}> 
                {this.state.admin && <Redirect to="/packages" />}
              </Route>
              <Route path="/contact" component={Contact} />
              <Route path="/about" component={About} />
              <Route path="/packages" render={(props) => <Packages runLoader={this.runLoader} {...props}/>} />
              <Route path="/reset" component={ResetPassword}/>
              <Route path="/store" render={(props) => <Shop getCaptions={this.getCaptions} runLoader={this.runLoader} loggedIn={this.state.loggedIn} admin={this.state.admin} {...props}/>}/>
              <Route path="/info" component={SiteInfo} />
              <Route component={NotFound}/>
            </Switch>
          </div>
        </Router>
        </ThemeProvider>
        </Suspense>
    )
  }
}

export default App
