import React, { Component } from 'react'
import { Helmet } from 'react-helmet';

import { Snackbar, Button, FormControlLabel, Checkbox, Input, Dialog, DialogTitle, DialogContent, InputLabel, FormControl, TextField, DialogActions, Select, MenuItem } from '@material-ui/core';
import Slider from 'react-slick';

import AddPhotoAlternateIcon from '@material-ui/icons/AddPhotoAlternate';
import DeleteIcon from '@material-ui/icons/Delete';

import { imageToBase64 } from './ImageConvertor.js';

const axios = require('axios');
const categories = ['Weddings', 'Urban', 'Modeling', 'Landscape', 'Graduations', 'Family Portraits'];

let scrolling = false;
let listener = false;

export class Portfolio extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            renderedPhotos: [],
            notification: '',

            images: [],
            captions: [],
            categories: [],

            currentImage: '',
            currentCaption: '',
            currentCategory: 0,

            captionValid: true,
            categoryValid: true,

            after: undefined,
            prompt: false,

            success: '',
            errors: '',

            filters: [['Weddings', false], ['Urban', false], ['Modeling', false], ['Landscape', false], ['Graduations', false], ['Family Portraits', false]],
            formattedCategories: []
        }

        this.reRender = this.reRender.bind(this);
        this.updateUploadAfter = this.updateUploadAfter.bind(this);
        this.updateUploadBefore = this.updateUploadBefore.bind(this);
        this.updateUpload = this.updateUpload.bind(this);
        this.updateCaption = this.updateCaption.bind(this);
        this.updateCategory = this.updateCategory.bind(this);
        this.finishUpload = this.finishUpload.bind(this);
        this.confirmDelete = this.confirmDelete.bind(this);
    }
    
    componentDidMount() {
        this.props.getCaptions((res) => {this.reRender(res.data)});
        if (!listener) {
            listener = true;
            for (let i = 0; i > 20; i++) {
                document.removeEventListener('keydown', this.rightAndLeft);
                document.removeEventListener('wheel', this.rightAndLeft);
            }
            document.addEventListener('keydown', (e) => this.rightAndLeft(e), {passive: true})
            document.addEventListener('wheel', (e) => this.rightAndLeft(e), {passive: true})
        }
    }

    componentWillUnmount() {
        document.removeEventListener('keydown', this.rightAndLeft)
        document.removeEventListener('wheel', this.rightAndLeft)
        listener = false;
    }

    rightAndLeft(e) {
        if (!this.state.prompt) {
            if (e.key === 'ArrowLeft') {
                document.querySelector('.slick-prev').click();
            } else if (e.key === 'ArrowRight') {
                document.querySelector('.slick-next').click();
            } else if (e.deltaY < 0 && !scrolling) {
                document.querySelector('.slick-next').click();
                scrolling = true;
                setTimeout(() => scrolling = false, 750);
            } else if (e.deltaY > 0 && !scrolling) {
                document.querySelector('.slick-prev').click();
                scrolling = true;
                setTimeout(() => scrolling = false, 750);
            }
        }
    }

    updateFilters(index) {
        let filters = [...this.state.filters];
        filters[index][1] = !filters[index][1];
        this.setState({filters}, () => this.props.getCaptions((res) => {this.reRender(res.data)}));
    }

    updateUploadBefore(event) {
        this.setState({after: false}, () => this.updateUpload(event));
    }

    updateUploadAfter(event) {
        this.setState({after: true}, () => this.updateUpload(event));
    }

    updateUpload(event) {
        for (let i = 0; i < event.target.files.length; i++) {
            imageToBase64(event.target.files[i]).then((result) => {
                if (result !== "error") {
                    this.setState({prompt: true, currentImage: result, success: '', errors: '', currentCaption: event.target.files[i]['name'].replace(/\.[^/.]+$/, "")})
                } else {
                    this.setState({notification: "Only images are supported."})
                    setTimeout(() => this.setState({notification: ''}), 2000);
                }
            });
        }
    }

    updateCategory(event) {
        this.setState({currentCategory: event.target.value, categoryValid: true})
    }

    updateCaption(event) {
        this.setState({currentCaption: event.target.value, captionValid: true})
    }

    finishUpload() {
        let captionValid = false;
        let categoryValid = false;
        let errors = [];

        if (this.state.currentCaption.length > 0) {
            captionValid = true;
        } else {
            errors.push(<p>Please enter a caption.</p>);
        }

        if (typeof this.state.currentCategory.length !== 'string') {
            categoryValid = true;
        } else {
            errors.push(<p>Please select a category.</p>);
        }

        if (captionValid && categoryValid) {
            this.setState({success: "Loading..."})
            axios.post('/addpics', {
                files: [this.state.currentImage],
                captions: [this.state.currentCaption],
                category: [this.state.currentCategory],
                after: this.state.after,
                ind: document.querySelector('.slick-active').children[0].children[0].children[0].src
            }).then(() => {
                this.setState({errors, success: "Successfully uploaded photo.", captionValid, categoryValid})
                this.props.getCaptions((res) => {this.reRender(res.data)});
                setTimeout(() => this.setState({prompt: false, currentCaption: '', currentCategory: 0}), 1500);
            }).catch(err => {
                if (err.response.status === 413) {
                    this.setState({success: '', errors: "File too large.", captionValid, categoryValid})
                } else if (err.response.status === 409) {
                    this.setState({success: '', errors: "Same photo in portfolio already exists.", captionValid, categoryValid})
                } else {
                    this.setState({success: '', errors: "Error uploading image, please try again later.", captionValid, categoryValid})
                }
            })
        } else {
            this.setState({errors, captionValid, categoryValid});
        }
    }

    confirmDelete() {
        if (document.querySelector('.slick-active') !== null) {
            axios.delete('/deletepics', {data: {file: document.querySelector('.slick-active').children[0].children[0].children[0].src}}).then(() => {
                this.setState({notification: 'Deleted photo.'})
                this.props.getCaptions((res) => {this.reRender(res.data)});
                setTimeout(() => this.setState({notification: ''}), 2000);
            });
        } else {
            this.setState({notification: 'No photos to delete.'})
            setTimeout(() => this.setState({notification: ''}), 2000);
        }
    }

    reRender(captions) {
        this.setState({
            formattedCategories: 
            this.state.filters.map((category, index) => {
                return (<FormControlLabel key={category[0]}
                control={
                <Checkbox
                    checked={this.state.filters[index][1]}
                    onChange={() => this.updateFilters(index)}
                    name={category[0]}
                    color="primary"
                />
                }
                label={category[0]}
            />);
            })}, () => {

                let imageCounter = 0;
                let finishCount = 0;
                let formattedImages = [];

                const noneSelected = !this.state.filters.map(filter => filter[1]).includes(true);

                for (let i = 0; i < captions.length; i++) {
                    if (this.state.filters[captions[i]['category']][1] || noneSelected) {
                        finishCount++;
                    }
                }

                const imagesRendered = () => {
                    return new Promise((resolve, reject) => {
                        (function waitForImages() {
                            if (imageCounter === finishCount) {
                                return resolve();
                            }
                            setTimeout(waitForImages, 30);
                        })()
                    })
                }
    
                for (let i = 0; i < captions.length; i++) {
                    // console.log(captions[i]);
                    // console.log(this.state.filters[captions[i]['category']][1]);
                    if (this.state.filters[captions[i]['category']][1] || noneSelected) {
                        const image = captions[i]['file'];
                        let img = new Image();
                        img.src = image;
                        img.onload = () => {
                            formattedImages[i] = (
                                <div className={`image`} key={image + Math.random()}>
                                    <img src={image} className={`formattedImage ${image.naturalWidth < image.naturalHeight ? 'portrait' : 'landscape'}`}/>
                                    <div className="captionOverlay">
                                        <p>{captions[i]['caption']},<br/> <i>{categories[captions[i]['category']]}</i></p>
                                    </div>
                                </div>)
                            imageCounter++;
                        }
                    }
                }

                imagesRendered().then(() => {
                    console.log(formattedImages);
                    this.setState({formattedImages})
                })
        })
    }

    render() {
        return (
            <div className="Landing">
                <Helmet>
                    <title>Edit Store - Omar Hill Photography</title>
                </Helmet>
                <div className="portfolio">
                    <h1 className="title">Edit Store</h1>
                    <div className="content">
                        <div className="images">
                            <Slider dots speed={750} lazyLoad infinite={false}>
                                {this.state.formattedImages}
                            </Slider>
                        </div>
                        <div className="panels">
                            <div className="upload">
                                <label htmlFor="contained-button-file-before">
                                    <input
                                        accept="image/*"
                                        onChange={this.updateUploadBefore}
                                        style={{ display: 'none' }}
                                        id="contained-button-file-before"
                                        type="file"
                                    />
                                    <Button variant="contained" component="span"><AddPhotoAlternateIcon color="primary"/>Upload Before Current Photo</Button>
                                </label>
                            </div>
                            <div className="upload">
                                <label htmlFor="contained-button-file-after">
                                    <input
                                        accept="image/*"
                                        onChange={this.updateUploadAfter}
                                        style={{ display: 'none' }}
                                        id="contained-button-file-after"
                                        type="file"
                                    />
                                    <Button variant="contained" component="span"><AddPhotoAlternateIcon color="primary"/>Upload After Current Photo</Button>
                                </label>
                            </div>
                            <div className="upload">
                                <Button onClick={this.confirmDelete} variant="contained" component="span"><DeleteIcon color="primary"/>Delete Current Photo</Button>
                            </div>
                            <div className="filters">
                                <p>Categories</p>
                                {this.state.formattedCategories}
                            </div>
                        </div>
                    </div>
                </div>
                <Snackbar
                    open={this.state.notification.length > 0}
                    anchorOrigin={{vertical: 'top', horizontal: 'center'}}
                    message={this.state.notification}
                    autoHideDuration={1500}
                />
                <Dialog className="uploadPrompt" open={this.state.prompt} onClose={() => this.setState({prompt: false, currentCaption: '', currentCategory: 0})}>
                    <DialogTitle>Upload Image</DialogTitle>
                    <DialogContent>
                    <img src={this.state.currentImage} alt="uploaded"></img>
                    <div className="fields">
                        <TextField
                            id="name"
                            label="Caption"
                            fullWidth
                            variant="outlined"
                            value={this.state.currentCaption}
                            onChange={this.updateCaption}
                            error={!this.state.captionValid}
                        />
                        <FormControl variant="outlined" className="dropdown" error={!this.state.categoryValid}>
                            <InputLabel>Photo Category</InputLabel>
                            <Select label="Photo Category" value={this.state.currentCategory} onChange={this.updateCategory}>
                                <MenuItem value={0}>Weddings</MenuItem>
                                <MenuItem value={1}>Urban</MenuItem>
                                <MenuItem value={2}>Modeling</MenuItem>
                                <MenuItem value={3}>Landscape</MenuItem>
                                <MenuItem value={4}>Graduations</MenuItem>
                                <MenuItem value={5}>Family Portraits</MenuItem>
                            </Select>
                        </FormControl>
                    </div>
                    {this.state.errors.length > 0 && <p className="errors">{this.state.errors}</p>}
                    {this.state.success.length > 0 && <p className="success">{this.state.success}</p>}
                    </DialogContent>
                    <DialogActions>
                    <Button onClick={() => this.setState({prompt: false, currentCaption: '', currentCategory: 0})}>Cancel</Button>
                    <Button onClick={this.finishUpload}>Upload</Button>
                    </DialogActions>
                </Dialog>
            </div>
        )
    }
}

export default Portfolio
